import validator from '../../utils/validator.js'
import T from '@/utils/tips.js'
import { postUserLogin, getUserInfoData, postUserSms, weixinLogin, openIdByCode } from '@/api/userApi.js'
export default {
	data() {
		return {
			phone:'',
			code:'',
			codeText: '获取验证码',
			codeNum: '', // 定时器时间
			isRight: false, // 是否完成输入
			setCodeInterval: '', // 定时器
			deviceId: '', // 数据传值deviceId
			appID: 'wxb8afa388fa540c2a',
			weixinCode:''
		}
	},
	components: {
	
	},
	onHide() {
		if(this.setCodeInterval!=''){
			clearInterval(this.setCodeInterval)
		}
	},
	onLoad() {
		
	},
	onShow() {
		uni.setStorageSync('isLogin',0)	
	},
	methods: {
		// 去用户协议
		goProtocal(){
			uni.navigateTo({
				url:'/pages/user/protocal/protocal'
			})
		},
		// 获取openid
		getOpenIdByCode(){
			uni.login({
				provider:'weixin',
				success(e) {
					console.log('code',JSON.stringify(e.code))
					let data = {
						code: e.code,
						providerId: 'miniProgram' 
					}
					console.log(data)
					openIdByCode(data).then(res => {
						console.log(res)
						if(res.code == '1000'){
							uni.setStorageSync('openid',res.data)
						}
					})
				}
			})
			
		},
		// APP微信登录
		wxLogin(){
			let _this = this
			uni.getProvider({
					service: 'oauth',
					success: function(res) {
						console.log(res.provider);
						//支持微信、qq和微博等
						if (~res.provider.indexOf('weixin')) {
							uni.login({
								provider: 'weixin',
								success: function(res) {
									console.log('-------获取openid(unionid)-----');
									console.log(JSON.stringify(res));
									let accessToken = res.authResult.access_token;
									let openId       = res.authResult.openid;
									let data = {
										grant_type: 'wx_app',
										scope: 2,
										client_id: 'cwap',
										client_secret: 'xx',
										systemId: 2,
										deviceId: _this.getUUID(), 
										accessToken,
										openId
									}
									console.log('data->>',data)
									postUserLogin(data).then(res=>{
										console.log(JSON.stringify(res))
										if(res.code == '9999'){
											uni.getUserInfo({
												provider: 'weixin',
												success: function(infoRes) {
													console.log('-------获取微信用户所有-----');
													console.log(JSON.stringify(infoRes.userInfo));
													data.userInfo = infoRes.userInfo
													uni.navigateTo({
														url:'/pages/login/binding/binding?data='+ JSON.stringify(data)
													})
												}
											});
										}else{
											uni.setStorageSync('access_token', res.access_token)
											uni.setStorageSync('refresh_token', res.refresh_token)
											uni.setStorageSync('uid', res.id)
											// 获取用户信息
											_this.getUserInfoDates()
										}
									})
									
								}
							});
						}
					}
				});
		},
	    // 小程序微信登录
	    getuserinfox(e) {
			let _this = this;
			console.log(e)
			uni.login({
			  provider: 'weixin',
			  success: function (loginRes) {
				console.log(loginRes);
				let data = {
					grant_type: 'mini_program',
					scope: 2,
					client_id: 'cwap',
					client_secret: 'xx',
					systemId: 2,
					deviceId: _this.getUUID(), 
					miniCode: loginRes.code
				}
				console.log('data->',data)
				postUserLogin(data).then(res=>{
					if(res.code == '9999'){
						uni.getUserInfo({
							provider: 'weixin',
							success: function(infoRes) {
								console.log('-------获取微信用户所有-----');
								console.log(JSON.stringify(infoRes.userInfo));
								data.userInfo = infoRes.userInfo
								uni.navigateTo({
									url:'/pages/login/binding/binding?data='+ JSON.stringify(data)
								})
							}
						});
					}else{
						uni.setStorageSync('access_token', res.access_token)
						uni.setStorageSync('access_token', res.refresh_token)
						uni.setStorageSync('uid', res.id)
					
						// 获取用户信息
						_this.getUserInfoDates()
					}
				})
			  }
			});
			
	    },
		doIsLogin() {
			this.isRight = !validator.isPhone(this.phone) && this.code !== ''
		},
	    // 获取验证码
	    getCode() {
	      console.log(validator.isPhone(this.phone))
	      // 已发送未超过60秒直接返回
	      if (this.codeNum !== '') {
	        return false
	      }
	      // 手机验证
	      if (validator.isPhone(this.phone)) {
	        if (this.phone === '') {
			  T.tips('手机号不能为空');
	          return false
	        }
			T.tips('手机号码不正确');
	        return false
	      }
		  
	      // 获取uuid
	      this.deviceId = this.getUUID()
	      // 获取手机验证码
	      let data = {
	        mobile: this.phone,
	        deviceId: this.deviceId
	      }
	      postUserSms(data).then((res) => {
			T.tips(res.message)
	        if (res.code === '1000') {
	          this.codeText = '重新发送'
	          this.codeNum = 59
	          this.setCodeInterval = setInterval(() => {
	            if (this.codeNum === 0) {
	              this.codeNum = ''
	              clearInterval(this.setCodeInterval)
	            } else {
	              this.codeNum--
	            }
	          }, 1000)
	        }
	      }).catch((err) => {
			T.tips(err.message || '错误')
	      })
	    },
	    // 登录
	    dologin: function () {
	      if (this.isRight) {




			  this.deviceId = this.getUUID()

	        let data = {
	          grant_type: 'sms_code',
	          scope: '2',
	          client_id: 'cwap',
	          client_secret: 'xx',
	          systemId: '2',
	          deviceId: this.deviceId,
	          mobile: this.phone,
	          smsCode: this.code
	        }
	        // let dates = this.$qs.stringify(data)
	        postUserLogin(data).then((res) => {
				uni.setStorageSync('access_token', res.access_token)
				uni.setStorageSync('refresh_token', res.refresh_token)
				uni.setStorageSync('uid', res.id)
				
				uni.setStorageSync('phone', this.phone)
				if(this.setCodeInterval!=''){
					clearInterval(this.setCodeInterval)
				}
	          // 获取用户信息
	          this.getUserInfoDates()
	        }).catch((err) => {
				T.tips(err.message || '登录错误')
	        })
	      }
	    },
	    getUserInfoDates() {

			this.getOpenIdByCode();

			
		  
	      getUserInfoData().then((res) => {
	        if (res.code === '1000') {
			  if(res.data.phone){
				uni.setStorageSync('phone', res.data.phone)
			  }	
	          uni.setStorageSync('nickName', res.data.nickName)
	          uni.setStorageSync('headImgUrl', res.data.headImgUrl)
			  // 返回上一页
			  uni.navigateBack({
				  delta:1
			  })
	        }
	      }).catch((err) => {
			T.tips(err.message || '获取用户信息错误')
	      })
	    },
	    getUUID() {
	        var s = [];
			var hexDigits = "0123456789abcdef";
			for (var i = 0; i < 36; i++) {
			  s[i] = hexDigits.substr(Math.floor(Math.random() * 0x10), 1);
			}
			s[14] = "4"; // bits 12-15 of the time_hi_and_version field to 0010
			s[19] = hexDigits.substr((s[19] & 0x3) | 0x8, 1); // bits 6-7 of the clock_seq_hi_and_reserved to 01
			s[8] = s[13] = s[18] = s[23] = "-";
		   
			var uuid = s.join("");
			return uuid
	    }
	  }
}
